import React, { Component } from 'react'
import Coin from './Coin';
import './CoinFlipper.css';
import { choice } from './helpers'

class CoinFlipper extends Component {
    static defaultProps = {
        coins: [
            { side: 'heads', imgSrc: 'https://tinyurl.com/react-coin-heads-jpg' },
            { side: 'tails', imgSrc: 'https://bjc.edc.org/June2017/bjc-r/img/5-algorithms/img_flipping-a-coin/Tails.png' }
        ]
    }
    constructor(props) {
        super(props);
        this.state = {
            currCoin: null,
            nflipps: 0,
            nHeads: 0,
            nTails: 0
        }

    }
    fliper() {
        let newCoin = choice(this.props.coins);
        this.setState(currState => ({
            currCoin: newCoin,
            nflipps: currState.nflipps + 1,
            nHeads: currState.nHeads + (newCoin.side === 'heads' ? 1 : 0),
            nTails: currState.nTails + (newCoin.side === 'tails' ? 1 : 0)
        }));
    }

    render() {
        return (
            <div className="CoinFlipper">
                <h1>Let's flip a coin!</h1>
                {this.state.currCoin !== null && <Coin src={this.state.currCoin.imgSrc} />}
                <button onClick={this.fliper.bind(this)}>Flip Me!</button>
                <p>Out of {this.state.nflipps} flips, there have been {this.state.nHeads} heads and {this.state.nTails} tails.</p>
            </div>
        )
    }
}
export default CoinFlipper;